import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Page.css';
import './video-react.css';
import { Player } from 'video-react';
import Event from './Event.js';
import data from './data/data.json'
import home from "./imgs/home.png"


class Page extends Component {
  constructor(props){
    super(props);
    this.addContent = this.addContent.bind(this);
    this.state ={
      
      legend : <div className="legend"><div className="block1">
      <h3>{data[0].event.title}</h3>
      <img src={data[0].event.picUrl} />
      </div>
      <div className="block2">
      <p>{data[0].event.details}</p>
      </div>
      </div>
    }
    this.clicked = this.clicked.bind(this);
  }
  addContent(){
    this.props.onClick(this.props.pageData.id, this.props.pageData.title);
  }

  clicked(e){
    this.setState({
      legend : <div className="legend"><div className="block1">
      <h3>{e.title}</h3>
      <img src={e.photo} />
      </div>
      <div className="block2">
      <p>{e.details}</p>
      </div>
      </div>
    });
  }
  render() {
    console.log(data);
    let homeButton = ""
    if(this.props.pageData.needshome){
      homeButton = <Link id="homeB" to="/3"><img  alt="Go to Homepage" src={home} width="50" height="50" /></Link>
    }


    let f =""
    let isViz = ""
    if (this.props.pageData.data === 1){
       let d =data;
       f = d.map(c =>{
      return <Event onClick={this.clicked} title={c.event.title} date={c.event.date} details={c.event.details} importance={c.event.importance} nature={c.event.nature} photo={c.event.picUrl} />

    } 
    
);
isViz = <div className="viz">{f} {this.state.legend}</div>
    }else if (this.props.pageData.data === 2){
      isViz=<div className="mainMenu">
      <Link to="/4"><button onClick={this.addContent} ID="bone">Orban in the parliament</button></Link>
      <button ID="btwo">Reforming the constitution</button>
      <button ID="bthree">War against free press</button>
     <button ID="bfour">International relations</button>

      </div>

    }else{
      f = ""
    }
    

    let vid;
    if (this.props.pageData.video !== ""){
      
      vid = <div className="bigVid"><Player
      playsInline
      poster="http://imanas.shanghai.nyu.edu/~mm7134/Capstone/pics/orban_fideszleader.jpg"
      src={this.props.pageData.video}
    /></div>
    }
      let pageText;
      if (this.props.pageData.text!==""){
        if (this.props.pageData.tOr === "left"){
        pageText = <div className="text_left">
          {this.props.pageData.text}
        </div>
      } else if (this.props.pageData.tOr === "right"){
          pageText = <div className="text_right">
          {this.props.pageData.text}
        </div>
      } else if (this.props.pageData.tOr === "bottom"){
        pageText = <div className="text_bottom">
          {this.props.pageData.text}
        </div>

      }else if (this.props.pageData.tOr === "top"){
        pageText = <div className="text_top">
          {this.props.pageData.text}
        </div>

      }

      } else {
        pageText=""
      }
      let forward;
      const linkTo = this.props.pageData.id+1;
    if (this.props.pageData.isF && this.props.pageData.title !== "Homepage"){

      forward = <Link onClick={this.addContent} className="forward" to={"/"+linkTo.toString()}></Link>;
    } else{
      forward = "";
    }
    let back;
    let backTo;
    if(this.props.pageData.id !== 1){
      backTo = this.props.pageData.id-1;
      back = <Link  className="back" to={"/"+backTo.toString()}></Link>;
    } else {
      back = "";
    }




    let the_title=""
    if (this.props.pageData.title !== "Homepage"){
      the_title=<h1>{this.props.pageData.title}</h1>
    }

    return (
      <div className="Page">
      
      {the_title}
        {homeButton}
        {vid}
        {pageText}
        {forward}
        {back}
        {isViz}

        



      </div>
    );
  }
}

export default Page;


