import React, { Component } from 'react';
import './App.css';
import content_header from "./imgs/content_header.png"
import Page from './Page'
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
<script src="https://cdn.rawgit.com/abdennour/react-csv/6424b500/cdn/react-csv-latest.min.js" type="text/javascript"></script>




class App extends Component {
  constructor(props) {
    super(props);
    this.added=this.added.bind(this);
    this.init=this.init.bind(this);
    this.state = {
      isLoading : true,
      contents: [
      {id: 1, title: "Introduction 1: After communism"}
      ],
      pages: [
      {id: 1, needshome: false, title:"Introduction 1: After communism", video:"http://imanas.shanghai.nyu.edu/~mm7134/Capstone/videos/vid%231ready.mp4",tOr: "bottom", text:"Welcome to [Project title]! This interactive project aims to show the current political state of Hungary through a series of videos and data visualization. You may click the button on the right bottom corner to go to the next frame. You can access the table of contents in the top right corner.", isF:true, data:""},
      {id: 2, needshome: false, title:"Introduction 2: History of a man", video:"", tOr: "top", text:"First, let's take a look at the main turning points in Orbn's life. These events provide a roadmap to the political landscape in post-communist Hungary, and show the unprecedented rise of the politician.", isF:true, data:1},
      {id: 3, needshome: false, title:"Homepage",video:"", text:"Now let's take a look at some of the most important aspects of this illustrious career. The next four segments explore Orban's actions on different fronts. You can always pause a segment and return to this page.", tOr: "top", isF:true, data:2},
      {id: 4, needshome: true, title:"Orban in the parliament 1.", video:"",text:"Sample text 4",tOr: "right", isF:false, data:""}
      ]
    };
  }

  init(){
    this.setState({
      isLoading: false
    });
  }
  added(a,b){
    if (a!== 1){
  let vane = false;  
  let copy = this.state.contents.slice();
 for(var i in copy){
      if(copy[i].id===a){
        vane = true;
      }
    };
    if (vane===false){
    copy.push({id: a, title: b});
   }
    this.setState({
      contents: copy
    });
  }

  }


  render() {
    let startButton;
    console.log(window.location.pathname);
    if (window.location.pathname ==="/"){
      startButton=<Link to="/1"><button onClick={this.init} className="the_start">Start</button></Link>
    } else{
      startButton=""
    }
    let cont0 = this.state.contents.slice();
    let cont1=cont0.map(c=>(
    <Link to={"/"+c.id.toString()} key={c.id}>{c.title}</Link>
    ));
   

    return (
      <Router>
        <div className="App">
        {startButton}
          <Route
            path="/:id"
            render={props => {
              // Find contact
              const a_page = this.state.pages.find(
                c => c.id === parseInt(props.match.params.id,10)
              );

              // Pass to component as prop
              return <Page pageData={a_page} onClick={this.added} />;
            }}
          />


<div className="contents">
          <a id="hdr"><img  alt="List of contents" src={content_header} width="60" height="60" /></a>
          <div className="dropdown">
            <h2>Contents:</h2>
            <div className="contList">{cont1}</div>
          </div>
        </div>


         </div>
        
      </Router>
    );
  }
}

export default App;


